.button {
  background-color: hsl(180, 100%, 25%);
  color: #fff;
  border: none;
  padding: 10px;
  cursor: pointer;
  transition: transform 1s, background-color 1s;
}

.button:hover {
  background-color: hsl(180, 100%, 30%);
  transform: scale(1.1);
}

.buttons {
  display: flex;
  flex-wrap: wrap;
}

.buttons__button {
  background: transparent;
  color: hsl(180, 100%, 25%);
  border: none;
  margin: 0 15px;
  transition: color 0.5s;
  cursor: pointer;
  padding: 2px 5px;
  ;
}

@media (max-width: 767px) {
  .buttons__button {
    margin: 10px;
    width: 100%;
  }
}

.buttons__button:hover {
  color: hsl(180, 100%, 35%);
}

.buttons__button:disabled {
  color: #ccc;
}

@media (max-width:767px) {
  .buttons__button {
    margin-bottom: 10px;
  }
}